@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
* {
    box-sizing: border-box;
}

button {
    background: none;
    border: none;
    -webkit-user-select: none;
    user-select: none;
    cursor: pointer;
}

.draggable {
    -webkit-app-region: drag;
    user-select: none;
}

.no_drag {
    -webkit-app-region: no-drag;
}

.hide {
    display: none;
}

.main {

    margin: 10px;
    display: flex;
    flex-direction: column;
    /* overflow-x: hidden; */
}

.windowCtr {
    display: flex;
    background: #383838;
    color: white;
    padding: 2px;
}

span.appName {
    flex: 1 1;
}

span.closeW {
    width: 22px;
    font-weight: bold;
    text-align: center;
}

span.minW {
    width: 22px;
    font-weight: bold;
    text-align: center;
}

.top {
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    flex-grow: 1;
    padding-bottom: 120px;
}

.left {
    flex: 1 1;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.right {
    padding: 10px;
    display: flex;
    flex-direction: column;
}


.graphics {
    padding: 15px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 1;
}

.header_h3 {

    font-weight: bold;
    margin: 0;
    padding: 10px;
    background: #4AA9DE;
    color:white;
}

.actions {
    background: #3a3a3a;
    color:white;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.actions_button {
    background: none;
    border: none;
    color: white;
    -webkit-user-select: none;
    user-select: none;
    width: 32px;
    height: 22px;
    cursor: pointer;
    margin: 0 10px ;
}
.action_btns{
    display: flex;
    flex-direction: row;
    flex:1;
    justify-content: center;
}
.album_meta{
    color: white;
    padding-left: 15px;
}
.alb{
    color: white;
}
.alb_label{
    font-size: 30px;
}
.playlist {
    background: #e3e3e3;
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.selected {
    background: white;
}

.playlist .header {

}

.songContainer {
    cursor: pointer;
    user-select: none;
}
.songContainerSelected{
    cursor: pointer;
    user-select: none;
    background: white;
}
.pltext {
    padding: 5px;
}

.loi {
    margin: 0 auto;
    list-style: none;
    display: flex;
    flex-direction: column;
    flex: 1 1;
    padding: 0;
    width: 70vw;
}

.loi li {
    margin: 2px;
    font-weight: 450;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.loi li:hover{
    background-color: #dfdfdf;
}




.tmbn_song {
    height: 65px;
    width: 65px;
    background:url('https://cdns-images.dzcdn.net/images/cover/80909d7a402560e429eb12746b932270/264x264.jpg');
    background-size: contain;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.tmbn_song1{
    font-size: 30px;
}
.tmbn_song1:hover{
    -ms-transform: scale(1.1);
}
.songlist {
    overflow-y: auto;
}
.songmeta{
    display: flex;
    flex-direction: column;
    flex: 1 1;

}
.songname {
    padding: 0 10px;
    font-weight: 600;
    font-size: 1.125rem;
}
.songauthors {
    padding: 0 10px;
    font-weight: normal;
    color: #555;
    font-size: 0.875rem;
}
.song_duration{
    color:#555;
}
.fav_song{
    color: red;
    margin-right: 5px;
}

.controls {
    display: flex;
    flex-direction: row;
    height: 110px;
    background: #3a3a3a;
    color: white;
    align-items: center;
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
}

.vlme {
    display: flex;
    align-content: center;
    overflow: hidden;
    width: 25px;
    transition: all 500ms;
    position: absolute;
    left: 0;
}

.vlme:hover {
    width: 90px;
}

.volum {
    padding: 10px;
}

#volBar {
    padding: 0;
    margin: 0;
    width: 50px;
    background: transparent;
}

#volBar::-moz-range-thumb {
    height: 10px;
    width: 3px;
    background: #2A6586;
    border-radius: 5px;
    cursor: pointer;
}

.musicControls {
    margin-left: 90px;
    min-width: 120px;
}

.musicControls span {
    cursor: pointer;
}

.musicControls span:hover {
    color: #2A6586;
}

.play {
    background: #4AA9DE;
    padding: 15px 18px;
    border-radius: 100%;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
    position: relative;
    z-index: 3;
}

.play:active {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
}

.play:hover {
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.4);
}

.prev {
    background: #4aa9de;
    border-radius: 10px 0 0px 10px;
    padding-left: 15px;
    padding-right: 10px;
    margin-right: -5px;
    z-index: 1;
}

.next {
    background: #4aa9de;
    border-radius: 0px 10px 10px 0px;
    padding-right: 15px;
    padding-left: 10px;
    margin-left: -5px;
    z-index: 1;
}

.progressb {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 20px;
    flex: 1;
}
.songMeta{
    width: 25vw;
}
.songtitle{
    display: block;
    font-size: 1.125em;
    font-weight: 600;
}
.songartistName{
    font-size: 0.8em;
}
#prgbar {
    background: transparent;
    width: 100%;
}

.plsoptions {
    display: flex;
    justify-content: space-evenly;
    width: 10vw;
}

.plsoptions span {
    cursor: pointer;
}

.plsoptions span:hover {
    color: #2A6586;
}

.plsoptions span.active {
    color: rgb(32, 77, 104);
}

.currentT, .totalT {
    width: 35px;
    margin: 0 10px;
}
input[type=range] {
    -webkit-appearance: none;
    margin: 10px 0;
    width: 100%;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000;
    background: #4aa9de;
    border-radius: 5px;
    border: 0px solid #000000;
}

input[type=range]::-webkit-slider-thumb {
    box-shadow: 0px 0px 1px #000000;
    border: 0px solid #000000;
    height: 13px;
    width: 4px;
    border-radius: 50px;
    background: #2a6586;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -4px;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: #4aa9de;
}

input[type=range]::-moz-range-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 0px 0px 0px #000000;
    background: #4aa9de;
    border-radius: 5px;
    border: 0px solid #000000;
}

input[type=range]::-moz-range-thumb {
    box-shadow: 0px 0px 1px #000000;
    border: 0px solid #000000;
    height: 13px;
    width: 4px;
    border-radius: 50px;
    background: #2a6586;
    cursor: pointer;
}

input[type=range]::-ms-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    color: transparent;
}

input[type=range]::-ms-fill-lower {
    background: #4aa9de;
    border: 0px solid #000000;
    border-radius: 10px;
    box-shadow: 0px 0px 0px #000000;
}

input[type=range]::-ms-fill-upper {
    background: #4aa9de;
    border: 0px solid #000000;
    border-radius: 10px;
    box-shadow: 0px 0px 0px #000000;
}

input[type=range]::-ms-thumb {
    box-shadow: 0px 0px 1px #000000;
    border: 0px solid #000000;
    height: 13px;
    width: 4px;
    border-radius: 50px;
    background: #2a6586;
    cursor: pointer;
}

input[type=range]:focus::-ms-fill-lower {
    background: #4aa9de;
}

input[type=range]:focus::-ms-fill-upper {
    background: #4aa9de;
}

.plsoptions {
    justify-self: flex-end;
}

.bttn{
    font-size: 30px;
}
.bttn1{
    font-size: 15px;
}
.bttn2{
    color: cornflowerblue;
    font-size: 15px;
}
.bttn3{
    font-size: 20px;
}